@import "../../scss/variables.scss";

.order-page {
  display: grid;

  @media (min-width: $tablet-width) {
    grid-template-columns: 80px 1fr;
    height: 100vh;
  }

  @media (min-width: $desktop-min-width) {
    grid-template-columns: 64px 1fr;
  }
}

.order-content {
  display: grid;
  grid-template-rows: auto auto 1fr;
}

.order-container {
  margin: 0;
  margin-top: 15px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    margin-left: auto;
    margin-right: auto;
    width: 1376px;
  }

  .order-container__content {
    height: 100%;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 2px 279px;
    }

    @media (min-width: $desktop-min-width) {
      grid-template-columns: 1fr 2px 389px;
    }

    .content__vertical-line {
      @media (min-width: $tablet-width) {
        margin-top: -33px;
        height: calc(100%+33px);
        border-left: 1px solid $gray-light;
      }
    }
  }
}

/* Breadcrumbs */

.breadcrumbs {
  margin-top: 30px;
  padding: 10px 0;
  border: 1px solid $gray-light;
  border-left-width: 0;
  border-right-width: 0;

  .order-container {
    margin-top: 0;
  }
  @media (min-width: $tablet-width) {
    margin-bottom: 32px;
  }
}

.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 10px;
  column-gap: 38px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: $gray;

  .breadcrumbs__item {
    position: relative;

    &:not(:last-child)::after {
      position: absolute;
      content: url("../../assets/images/breadcrumb.svg");
      right: -23px;
    }

    &.breadcrumbs__item_done {
      color: $black;
    }

    &.breadcrumbs__item_active {
      color: $main-accent;
    }
  }
}

.order-container__breadcrumbs {
  position: relative;

  &::after,
  &::before {
    position: absolute;
    left: auto;
    content: "";
    width: 100vw;
    right: -1em;
    height: 1px;
    z-index: -1;
    background-color: $gray;
    overflow-y: hidden;
  }

  &::before {
    top: -10px;
  }

  &::after {
    bottom: -10px;
  }
}

/* Point */

.content__point {
  width: 280px;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: $black;

  @media (min-width: $tablet-width) {
    width: 325px;
  }
}

.point {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  row-gap: 16px;

  .point__input[type="search"] {
    height: 20px;
    margin-left: 5px;
    border: 0;
    border-bottom: 1px solid $gray;
    background: $white;
    color: $black;

    &:focus {
      outline: 0;
      border: 0;
      border-bottom: 1px solid $black;
    }

    @media (min-width: $tablet-width) {
      width: 224px;
    }
  }

  .point__input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;
    height: 8px;
    width: 8px;
    background: url("../../assets/images/cancel.svg") no-repeat 50% 50%;
    background-size: contain;
    opacity: 0.5;
  }

  .point__input[type="search"]:focus::-webkit-search-cancel-button {
    opacity: 1;
  }

  .point__select {
    width: 224px;
    height: 20px;
  }
}

/* React Select */

.react-select__control {
  height: 20px;
  border: 0;
  border-bottom: 1px solid $gray;
  background: $white;
  color: $black;
}

/* Map */

.content__map {
  margin-top: 48px;
  width: 100%;
}

/* Order */

.order {
  margin-top: 20px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
    margin-left: 16px;
  }

  .order__header {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: right;
    color: $black;
  }

  .order__list {
    margin-top: 26px;
  }

  .order__item {
    display: flex;
    margin-top: 16px;
    justify-content: space-between;
    align-items: flex-end;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    color: $black;

    .order__name-item {
      max-width: 38%;
    }

    .order__dotted {
      flex-grow: 1;
      border-bottom: 1px dotted #000;
      height: 1em;
      margin: 0 0.4em;
    }

    .order__value-item {
      text-align: end;
      color: $gray;
      max-width: 35%;
    }
  }

  .order__price {
    margin-top: 32px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;

    .order__amount-money {
      font-weight: 400;
    }
  }

  .order__action {
    margin-top: 32px;
    width: 100%;
  }
}
